<template>
  <div class="mui-page">
    <header class="mui-bar mui-bar-nav" @click="back">
        <a class="mui-icon mui-icon-bars mui-pull-left mui-plus-visible"></a>
        <a class="mui-icon mui-pull-right"></a>
        <h1 class="mui-title">个人房源</h1>
      </header>
      <nav class="mui-bar mui-bar-tab">
        <a class="mui-tab-item" 
             href="javascript:;"
             :class="{'mui-active': params_key=='58'}"
             @click="set_key(58)">
          <span class="mui-icon icon-58logo iconfont"></span>
          <span class="mui-tab-label">58同城</span>
        </a>
        <a class="mui-tab-item" href="javascript:;"
             :class="{'mui-active': params_key=='xz'}"
             @click="set_key('xz')">
          <span class="mui-icon icon-xi iconfont"></span>
          <span class="mui-tab-label">西子湖畔</span>
        </a>
    </nav>
    <div class="mui-progressbar mui-progressbar-success mui-progressbar-infinite"
            v-if="is_loading">
    </div>
    <div class="mui-content-padded">
      <div id="segmentedControl" class="mui-segmented-control">
        <a class="mui-control-item" href="javascript:;"
             :class="{'mui-active': params_type=='sale'}"
             @click="set_type('sale')">
           <i class="iconfont">&#xe635;</i>
           售卖房源
        </a>
        <a class="mui-control-item" href="javascript:;"
                 :class="{'mui-active': params_type=='rent'}"
                 @click="set_type('rent')">
          <i class="iconfont">&#xe691;</i>
          出租房源
        </a>
      </div>
      <div class="mui-card" v-for="(item, index) in estate">
        <a :href="item.links">
          <div class="mui-card-header mui-card-media" :style="{backgroundImage: 'url('+item.pic+')', height: '40vw'}">
          </div>
        </a>
        <div class="mui-card-content">
          <div class="mui-card-content-inner">
            <p v-text="item.title" class="s-c_333"></p>
            <p v-cloak v-html="item.price"> </p>
          </div>
        </div>
        <div class="mui-card-footer">
          <a class="mui-card-link pull-left" v-text="item.address"></a>
          <a class="mui-card-link pull-right" v-text="item.date"></a>
        </div>
      </div>
    </div>
    <div class="mui-content-padded" v-if="estate.length>0">
        <ul class="mui-pagination">
          <li class="u-item" v-for="(item, index) in laypage"
                             :class="{'mui-active': cur_page==item.value}"
                             @click="set_page(item.value)">
            <a href="javascript:;" v-cloak class="u-txt">
              {{item.value}}
            </a>
          </li>
        </ul>
      </div>
      <div class="m-back" v-if="scrollY>700" @click="back"><i class="iconfont i-back">&#xe605;</i></div>
      <div class="m-for_footer"> </div>
  </div>
</template>

<script>
  require('../../static/js/zepto.min.js')

  import { mapState ,mapActions, mapMutations} from 'vuex'

  export default {
    name: 'search',
    data () {
      return {
        keyWord: '房地产',
        api: 'http://localhost:8090?query=',
        scrollY: 0
      }
    },
    mounted: function(){
      var vue=this;
      window.addEventListener('scroll', function(){
        vue.scrollY=window.scrollY;
      });
      vue.searchFun();
    },
    computed: {
      ...mapState({
        is_loading: 'is_loading',
        estate: 'estate',
        laypage: 'laypage',
        params_key: 'params_key',
        cur_page: 'params_page',
        params_type: 'params_type'
      }),
    },
   methods: {
      ...mapActions([
        'searchFun', // 映射 this.searchFun() 为 this.$store.dispatch('searchFun')
      ]),
      set_key: function(key){
        // 以对象形式分发
        this.$store.dispatch({
          type: 'changesite',
          key: key
        })
        // 回到顶部
        this.back();
      },
      set_type: function(type){
        // 以对象形式分发
        this.$store.dispatch({
          type: 'changetype',
          key: type
        })
        // 回到顶部
        this.back();
      },
      set_page: function(params_page){
        // 以对象形式分发
        this.$store.dispatch({
          type: 'changetab',
          key: params_page
        })
        // 回到顶部
        this.back();
      },
      back: function(){
        document.body.scrollTop = 0
        document.documentElement.scrollTop = 0
      }
    }
  }
</script>

<style scoped>

.s-c_333{
  color: #333;
}

.mui-card{
  text-align: left;
}

.pull-left{
  width: 80%;
}
.pull-right{
  text-align: right;
}
.mui-card-link{
  height: auto;
  line-height: 24px;
}

.mui-pagination{
  text-align: left;
}

.mui-pagination .u-item{
  text-align: center;
  margin: 10px auto;
}

.mui-pagination .u-txt{
  margin: 0 4px;
  border-color: transparent;
}

.mui-progressbar{
  position: fixed;
  top: 44px;
  left: 0;
  z-index: 2;
}
.m-for_footer{
  height: 50px;
}

.m-back{
  position: fixed;
  bottom: 70px;
  right: 20px;
}

.i-back{
  margin: 3px;
  width: 50px;
  height: 50px;
  font-size: 20px;
  line-height: 50px;
  display: inline-block;
  text-align: center;
  color: #999;
  background-color: #fff;
  border-radius: 25px;
  background-clip: padding-box;
}

.mui-title{
  color: #007aff;
}
</style>



